/**
 * клас Header призначений для обробки всього що відноситься і відбувається у шапці сайта
 */
class Header {
	constructor(
		private _headerBurger: HTMLElement,
		private _body: HTMLElement,
		private _headerContent: HTMLElement,
		private _mobMenuClose: HTMLElement,
		private _searchFormSearch: HTMLElement
	) {}

	/**
	 * Method add event click listeners on elements for opening and closing
	 * mobile menu.
	 * @param width on this width menu should hide into mobile menu
	 */
	mobMenu(width: number = 992): void {
		this._headerBurger.addEventListener('click', (): void => {
			if (window.innerWidth < width) {
				this._body.classList.add('lock');
				this._headerContent.classList.add('open');
			}
		});
		this._mobMenuClose.addEventListener('click', (): void => {
			this._body.classList.remove('lock');
			this._headerContent.classList.remove('open');
		});
	}

	/**
	 * метод searchForm() призначена для того щоб форма пошуку у шапці працювала
	 * @param form форма для пошуку
	 */
	searchForm(form: HTMLFormElement): void {
		this._searchFormSearch.addEventListener('click', (): void => {
			form.querySelector('.search-form__input')!.classList.add('show');
		});

		form.elements['close-form' as unknown as number].addEventListener(
			'click',
			e => {
				e.preventDefault();
				form.querySelector('.search-form__input')!.classList.remove('show');
				(
					form.elements['search-value' as unknown as number] as HTMLFormElement
				).value = '';
			}
		);

		form.addEventListener('submit', (e): void => {
			e.preventDefault();
			const searchValue: string = (
				form.elements['search-value' as unknown as number] as HTMLFormElement
			).value;
			if (searchValue) {
				const win: Window = window;
				win.location = `https://translate.google.com.ua/?sl=en&tl=uk&text=${searchValue}&op=translate`;
			}
		});
	}
}

export default Header;
