import Group from '../functions/Group';
import { defaultArray, infoObject, V } from '../types/types';

interface groupedObject {
	[index: V]: {
		[index: V]: defaultArray;
	};
}

/**
 * Class for moving DOM elements from one place to another.
 * Use data attribute in HTML: `data-mt="minWidth,1024,blue,afterend"`,
 *
 * where (`DO NOT DISTURB THE ORDER`):
 * 1. `minWidth` or `maxWidth`
 * 2. window `width`
 * 3. `id` or `class` (class without dot) of an element where to move (id starts with #)
 * 4. exect position: `beforebegin`, `afterbegin`, `beforeend`, `afterend`.
 *
 * To start using MoveElements just write `new MoveEements();`.
 *
 * Recomendation:
 * `don't use maxWidth and minWidth for neighboring elements, it can cause unpredictable behavour`
 *
 * Feature of script: if inside the parent element no children and script try to move there some child element,
 * script will insert it after begin of the parent element, so be careful in such situations,
 * because in this case order of elements can be changed
 */
export default class MoveElements {
	private elements: NodeListOf<HTMLElement>;
	private groupedInfoObject?: groupedObject;
	private maxWidth?: {
		[index: V]: defaultArray;
	};
	private minWidth?: {
		[index: V]: defaultArray;
	};
	private maxWidthKeys?: string[];
	private minWidthKeys?: string[];
	private mediaQueries?: string[];

	constructor() {
		this.elements = document.querySelectorAll('[data-mt]');
		if (this.elements.length) {
			this.groupedInfoObject = Group.groupObject(
				this.getMoveToInfo(),
				'width'
			) as groupedObject;
			this.maxWidth = this.groupedInfoObject.max;
			this.minWidth = this.groupedInfoObject.min;
			this.maxWidthKeys = Object.keys(this.maxWidth);
			this.minWidthKeys = Object.keys(this.minWidth);
			this.mediaQueries = [];
			this.init();
		} else {
			console.warn(
				'No elements to move! Please fill in "data-mt" attribute for html elements.'
			);
		}
	}

	private getMoveToInfo():
		| {
				max: infoObject[];
				min: infoObject[];
		  }
		| undefined {
		const elementsInfoArray = Array.from(this.elements).reduce(
			(arrToFill: infoObject[], elToMove: HTMLElement): infoObject[] => {
				const elInfo = elToMove.dataset.mt?.split(',');
				const getPositionInParent = () => {
					const children = elToMove.parentElement?.children;
					if (children) {
						for (let i = 0; i <= children.length; i++) {
							if (elToMove.isEqualNode(children[i])) {
								return i;
							}
						}
					}
				};
				return [
					...arrToFill,
					{
						condition: elInfo?.[0] ? elInfo[0] : 'maxWidth',
						width: elInfo?.[1] ? elInfo[1] : '1024',
						elemWhereToMove: elInfo?.[2],
						place: elInfo?.[3],
						element: elToMove,
						parent: elToMove.parentElement,
						positionInParent: getPositionInParent(),
					},
				];
			},
			[]
		);

		let moveToInfoMax: infoObject[] = [];
		let moveToInfoMin: infoObject[] = [];

		elementsInfoArray.forEach((infoObject: infoObject) => {
			switch (infoObject?.condition) {
				case 'maxWidth':
					moveToInfoMax = [...moveToInfoMax, infoObject];
					break;
				case 'minWidth':
					moveToInfoMin = [...moveToInfoMin, infoObject];
					break;
			}
		});

		return { max: moveToInfoMax, min: moveToInfoMin };
	}

	private moveElementsCallback(
		maxWidth: { [index: V]: defaultArray },
		maxWidthKeys: string[],
		minWidth: { [index: V]: defaultArray },
		minWidthKeys: string[]
	): void {
		const moveElementToNewPos = (objectWithInfo: infoObject) => {
			// check if element was moved earlier
			const whereToMove = objectWithInfo.elemWhereToMove.startsWith('#')
				? document.querySelector(`${objectWithInfo.elemWhereToMove}`)!
				: document.querySelector(`.${objectWithInfo.elemWhereToMove}`)!;

			switch (objectWithInfo.place) {
				case 'beforebegin':
					if (
						objectWithInfo.element.isEqualNode(
							whereToMove.previousElementSibling
						)
					)
						return false;
				case 'afterbegin':
					if (objectWithInfo.element.isEqualNode(whereToMove.firstElementChild))
						return false;
				case 'beforeend':
					if (objectWithInfo.element.isEqualNode(whereToMove.lastElementChild))
						return false;
				case 'afterend':
					if (
						objectWithInfo.element.isEqualNode(whereToMove.nextElementSibling)
					)
						return false;
			}

			// move element
			switch (objectWithInfo.place) {
				case 'beforebegin':
					whereToMove.before(objectWithInfo.element);
					break;
				case 'afterbegin':
					whereToMove.prepend(objectWithInfo.element);
					break;
				case 'beforeend':
					whereToMove.append(objectWithInfo.element);
					break;
				case 'afterend':
					whereToMove.after(objectWithInfo.element);
					break;
			}
		};

		const moveElementToOldPos = (objectWithInfo: infoObject) => {
			let prevElem;

			// check if our element is in his initial position
			if (
				objectWithInfo.element.isEqualNode(
					objectWithInfo.parent.children[objectWithInfo.positionInParent]
				)
			)
				return false;

			// move element after his neighbour element inside the parent element
			if (objectWithInfo.positionInParent !== 0) {
				prevElem =
					objectWithInfo.parent.children?.[objectWithInfo.positionInParent - 1];
				if (prevElem) prevElem.after(objectWithInfo.element);
				else objectWithInfo.parent.prepend(objectWithInfo.element);
			} else {
				objectWithInfo.parent.prepend(objectWithInfo.element);
			}
		};

		// working with maxWidth
		maxWidthKeys.forEach(width => {
			if (window.innerWidth <= +width) {
				maxWidth[width].forEach(objectWithInfo => {
					moveElementToNewPos(objectWithInfo as infoObject);
				});
			} else {
				// else we should move likely moved element to its initial position
				maxWidth[width].forEach(objectWithInfo => {
					moveElementToOldPos(objectWithInfo as infoObject);
				});
			}
		});

		// working with minWidth
		minWidthKeys.forEach(width => {
			if (window.innerWidth >= +width) {
				minWidth[width].forEach(objectWithInfo => {
					moveElementToNewPos(objectWithInfo as infoObject);
				});
			} else {
				// else we should move likely moved element to its initial position
				minWidth[width].forEach(objectWithInfo => {
					moveElementToOldPos(objectWithInfo as infoObject);
				});
			}
		});
	}

	private init(): void {
		Object.keys(this.groupedInfoObject!).forEach(mediaType => {
			Object.keys(this.groupedInfoObject![mediaType]).forEach(screenWidth => {
				this.mediaQueries?.push(`(${mediaType}-width: ${screenWidth}px)`);
			});
		});

		// this script will call moveElementsCallback function only after page is reloaded
		// if you want a layout react on every window reseze in real time you should
		// uncomment resize event listener below and comment cycle with matchMedia
		this.mediaQueries?.forEach(media => {
			if (window.matchMedia(media).matches) {
				this.moveElementsCallback(
					this.maxWidth!,
					this.maxWidthKeys!,
					this.minWidth!,
					this.minWidthKeys!
				);
			}
		});

		// this script can cause a heavy load of CPU
		// this.moveElementsCallback(
		// 	this.maxWidth!,
		// 	this.maxWidthKeys!,
		// 	this.minWidth!,
		// 	this.minWidthKeys!
		// );
		// window.addEventListener('resize', () =>
		// 	this.moveElementsCallback(
		// 		this.maxWidth!,
		// 		this.maxWidthKeys!,
		// 		this.minWidth!,
		// 		this.minWidthKeys!
		// 	)
		// );
	}
}
