'use strict';
import Page from './modules/Page';
import Header from './modules/Header';
import Swiper, { Navigation } from 'swiper';
import MoveElements from './modules/utils/MoveElements';

// set right property for header info
Page.workingWithHeaderInfo();
Page.windowResize();

const arrayForAnimateText = [
	['.text-in-bg', 'lover'],
	['.flowers__title-text', 'flower'],
];

const header = new Header(
	document.querySelector('.header__burger')!,
	document.querySelector('body')!,
	document.querySelector('.header__content')!,
	document.querySelector('.mob-menu-close')!,
	document.querySelector('.search-form__search')!
);
header.mobMenu();
header.searchForm(document.forms['search-form' as unknown as number]);

Page.bodyScroll({
	flowersCart: document.querySelector('.flowers__cart'),
	flowersBody: document.querySelector('.flowers__body'),
	body: document.querySelector('body'),
	headerContent: document.querySelector('.header__content'),
	header: document.querySelector('.header'),
	tel: document.querySelector('.info__tel-text'),
	infoTel: document.querySelector('.info__tel'),
});
// Page.showTextByChar(arrayForAnimateText);

new Swiper('.popular__slider', {
	navigation: {
		nextEl: '.slider-popular__button-next',
		prevEl: '.slider-popular__button-prev',
	},
	modules: [Navigation],
	slidesPerView: 3,
	// loop: true,

	breakpoints: {
		// when window width is >= 320px
		320: {
			enabled: false,
			spaceBetween: 0,
		},
		// when window width is >= 768px
		768: {
			enabled: true,
			spaceBetween: 20,
		},
		992: {
			spaceBetween: 30,
		},
	},
});

new MoveElements();
