import { V } from '../types/types';
interface initialObject {
	[index: V]: any;
}
interface groupedObjectByProperty {
	[index: V]: initialObject[];
}

export default class Group {
	/**
	 * Input: [
	 *
	 * 	{ name: "Alice", age: 21 },
	 * 	{ name: "Max", age: 20 },
	 * 	{ name: "Jane", age: 20 },
	 * ]
	 *
	 * Output: {
	 *
	 * 	20: [
	 *		{ name: 'Max', age: 20 },
	 *		{ name: 'Jane', age: 20 }
	 *			],
	 * 	21: [{ name: 'Alice', age: 21 }]
	 * }
	 * @param objectArray array with objects
	 * @param property string
	 * @returns object with `property` as key and array with objects
	 * or `null` if objectArray is undefined or null
	 */
	static groupBy(
		objectArray: initialObject[] | undefined | null,
		property: string
	): groupedObjectByProperty | null {
		if (objectArray) {
			return objectArray.reduce((acc, obj) => {
				const key = obj[property];
				acc[key] ??= [];
				acc[key].push(obj);
				return acc;
			}, {});
		}
		return null;
	}

	/**
	 * This function converts arrays inside object onto
	 * objects with arrays grouped by `someKey`
	 *
	 * Input (someKey = 'age'): {
	 *
	 * 	max: [{ name: "Max", age: 20 },
	 * 		{ name: "Jane", age: 20 }],
	 * 	min: [{ name: "Alice", age: 21 }],
	 * }
	 *
	 * Output:{
	 *
	 * 	max: {
	 * 		20: [{ name: "Max", age: 20 },
	 * 		{ name: "Jane", age: 20 }],
	 * 	},
	 * 	min: {
	 * 		21: [{ name: "Alice", age: 21 }],
	 * 	}
	 * }
	 *
	 * @param {*} object with arrays
	 * @returns object with grouped objects with arrays or null if object is undefined or null
	 */
	static groupObject(
		object: groupedObjectByProperty | undefined | null,
		someKey: string
	): { [index: V]: groupedObjectByProperty } | null {
		if (object) {
			return Object.keys(object).reduce((groupedInfo, key) => {
				return { ...groupedInfo, [key]: this.groupBy(object[key], someKey) };
			}, {});
		}
		return null;
	}
}

export const groupBy = Group.groupBy;
// export const groupObject = Group.groupObject;
